import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import TableauAdmins from './TableauAdmins';
import TableauMembres from './TableauMembres';
import './SuperAdminDashboard.css';
import MemberDetailsModal from "./MemberDetailsModal";
import EditMemberModal from "./EditMemberModal";
import EditAdminModal from "./EditAdminModal";
import "./ModalStyle.css";


const SuperAdminDashboard = () => {
    const navigate = useNavigate();
    const [admins, setAdmins] = useState([]);
    const [members, setMembers] = useState([]);

    //  Protger la rcupration de `user` contre une erreur si `localStorage` est vide
    const user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : null;

    //  tats pour la gestion des modales
    const [editData, setEditData] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [selectedMember, setSelectedMember] = useState(null);
    const [showMemberDetailsModal, setShowMemberDetailsModal] = useState(false);
    const [showEditMemberModal, setShowEditMemberModal] = useState(false);

    //  Rcupration des administrateurs
    useEffect(() => {
        const fetchAdmins = async () => {
            const token = localStorage.getItem("token");
            const response = await fetch("https://mlm-app-jhc.fly.dev/api/auth/admins", {
                headers: { "Authorization": `Bearer ${token}` }
            });

            if (response.ok) {
                const data = await response.json();
                setAdmins(data);
            } else {
                console.error(" Erreur chargement des administrateurs.");
            }
        };
        fetchAdmins();
    }, []);

    //  Rcupration des membres
    useEffect(() => {
        const fetchMembers = async () => {
            const token = localStorage.getItem("token");
            const response = await fetch("https://mlm-app-jhc.fly.dev/api/members", {
                headers: { "Authorization": `Bearer ${token}` }
            });

            if (response.ok) {
                const data = await response.json();
                setMembers(data);
            } else {
                console.error(" Erreur chargement des membres.");
            }
        };
        fetchMembers();
    }, []);

    //  Fonction pour ouvrir la fentre d'inscription
    const openRegistrationWindow = () => {
        const registrationUrl = window.location.origin + "/#/inscription";
        window.open(
            registrationUrl,
            "InscriptionUtilisateur",
            "width=500,height=650,top=100,left=100"
        );
    };

    return (
        <div className="superadmin-dashboard">
            <h1 className="dashboard-title"> Tableau de bord Super Admin</h1>
            <p className="dashboard-message">Bienvenue sur votre espace de gestion.</p>

            {/*  Boutons d'accs */}
            <div className="admin-buttons">
                <button onClick={() => navigate("/member-dashboard")} className="btn-dashboard-user btn-medium">
                     Tableau de bord User
                </button>
                <button onClick={openRegistrationWindow} className="btn-add-user btn-medium">
                     Inscrire un utilisateur
                </button>
            </div>

            {/*  Liste des administrateurs */}
            <div className="admin-table">
                <TableauAdmins 
                    admins={admins} 
                    onEdit={(admin) => setEditData(admin) || setShowEditModal(true)} 
                    onDelete={(adminEmail) => {/* Gestion suppression admin */}} 
                    onView={(admin) => navigate(`/admin/${admin._id}`)}
                />
            </div>

            {/*  Liste des membres */}
            <div className="member-table">
                <TableauMembres 
                    members={members} 
                    onEdit={(member) => setEditData(member) || setShowEditModal(true)}
                    onDelete={(memberEmail) => {/* Gestion suppression membre */}}
                    onView={(member) => {
                        setSelectedMember(member);
                        setShowMemberDetailsModal(true);
                    }}
                    onHistory={(member) => navigate(`/member/${member._id}/history`)}
                />
            </div>

            {/*  Modales */}
            {showEditModal && <EditAdminModal admin={editData} onClose={() => setShowEditModal(false)} />}
            {showMemberDetailsModal && <MemberDetailsModal member={selectedMember} onClose={() => setShowMemberDetailsModal(false)} />}
            {showEditMemberModal && <EditMemberModal member={selectedMember} onClose={() => setShowEditMemberModal(false)} />}
        </div>
    );
};

export default SuperAdminDashboard;
