import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const ProductsEnr = () => {
    const [products, setProducts] = useState([]);
    const [error, setError] = useState(null);
    const navigate = useNavigate();

    const user = JSON.parse(localStorage.getItem("user"));
    const memberId = user ? user._id : null;
    const isMember = user && user.role === "member"; //  Vrifie si c'est un membre

    useEffect(() => {
        if (!memberId) {
            setError(" Impossible de rcuprer l'ID du membre.");
            return;
        }

        const fetchSubscribedProducts = async () => {
            try {
                const token = localStorage.getItem("token");
                const url = `https://mlm-app-jhc.fly.dev/api/products/member/${memberId}`;

                const response = await fetch(url, {
                    headers: { "Authorization": `Bearer ${token}` }
                });

                const data = await response.json();

                if (!response.ok) {
                    throw new Error(data.error || "Erreur lors du chargement des produits.");
                }

                setProducts(data);
            } catch (err) {
                setError(err.message);
            }
        };

        fetchSubscribedProducts();
    }, [memberId]);

    return (
        <div style={containerStyle}>
            <h1> Produits souscrits</h1>

            {error && <p style={{ color: "red" }}> {error}</p>}

            {isMember && (
                <button 
                    onClick={() => navigate("/products-liste")} 
                    style={subscribeButtonStyle}
                >
                     Souscrire  un produit
                </button>
            )}

            {products.length > 0 ? (
                <table style={tableStyle}>
                    <thead>
                        <tr>
                            <th>Nom</th>
                            <th>Montant Investi</th>
                            <th>Date de souscription</th>
                            <th>Dure</th>
                            <th>Rendement (%)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {products.map(product => (
                            <tr key={product._id}>
                                <td>{product.name}</td>
                                <td>{product.amountInvested}</td>
                                <td>{new Date(product.subscriptionDate).toLocaleDateString()}</td>
                                <td>{product.duration} mois</td>
                                <td>{product.yeld}%</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            ) : (
                <p> Aucun produit souscrit.</p>
            )}
        </div>
    );
};

//  Styles
const containerStyle = { maxWidth: "800px", margin: "auto", padding: "20px" };
const tableStyle = { width: "100%", borderCollapse: "collapse", marginTop: "20px" };
const subscribeButtonStyle = { padding: "10px", backgroundColor: "#007bff", color: "white", borderRadius: "5px" };

export default ProductsEnr;