import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const ProductsListe = () => {
    const [products, setProducts] = useState([]);
    const [error, setError] = useState(null);
    const navigate = useNavigate();
    const user = JSON.parse(localStorage.getItem("user"));
    const memberId = user ? user._id : null;

    useEffect(() => {
        fetch("https://mlm-app-jhc.fly.dev/api/products")
            .then(res => res.json())
            .then(data => setProducts(data))
            .catch(err => setError(" Erreur chargement des produits."));
    }, []);

    const subscribeToProduct = async (productId) => {
        try {
            const token = localStorage.getItem("token");
            const url = `https://mlm-app-jhc.fly.dev/api/products/subscribe/${productId}`;

            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Authorization": `Bearer ${token}`,
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({ memberId })
            });

            const data = await response.json();
            if (!response.ok) {
                throw new Error(data.error || "Erreur lors de la souscription.");
            }

            alert(" Souscription russie !");
            navigate("/products-enr");
        } catch (error) {
            alert(` Erreur : ${error.message}`);
        }
    };

    return (
        <div style={containerStyle}>
            <h1> Produits disponibles</h1>
            
            {error && <p style={{ color: "red" }}>{error}</p>}

            <table style={tableStyle}>
                <thead>
                    <tr>
                        <th>Nom</th>
                        <th>Montant Investi</th>
                        <th>Dure</th>
                        <th>Rendement (%)</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {products.map(product => (
                        <tr key={product._id}>
                            <td>{product.name}</td>
                            <td>{product.amountInvested}</td>
                            <td>{product.duration} mois</td>
                            <td>{product.yeld}%</td>
                            <td>
                                <button 
                                    onClick={() => subscribeToProduct(product._id)}
                                    style={subscribeButtonStyle}
                                >
                                     Ajouter
                                </button>
                                <button 
                                    onClick={() => navigate("/products-enr")} 
                                    style={cancelButtonStyle}
                                >
                                     Annuler
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

const containerStyle = { maxWidth: "800px", margin: "auto", padding: "20px" };
const tableStyle = { width: "100%", borderCollapse: "collapse", marginTop: "20px" };
const subscribeButtonStyle = { padding: "5px", backgroundColor: "#28a745", color: "white", borderRadius: "5px" };
const cancelButtonStyle = { padding: "5px", backgroundColor: "#dc3545", color: "white", borderRadius: "5px" };

export default ProductsListe;